<template>
    <component :is="tag" class="has-hint --clickable" :class="{'has-hint--hover': isHover}" :for="forInput" @click="showHint">
        {{label}}

        <slot></slot><transition name="hint-show"><span :style="styleHint" :class="[direction, {'open': show}]" v-show="show" class="hint">{{hint}}</span>
        </transition>
    </component>
</template>

<script>
    export default {
        name: "LabelHint",
        props:{
            hint : {
                default: 'Это обязательный блок, его нельзя отключить'
            },
            label: '',
            forInput: '',
            direction: {
                default: 'top-right-direction'
            },
            styleHint: {
                default: 'left: 8px'
            },
            tag: {
                type: String,
                default: function () {
                    return 'label'
                }
            },
            isHover: {
                default: false
            }
        },
        data(){
            return {
                show: false
            }
        },
        mounted(){
            if(!this.isHover){
                // document.onclick = (event) => {
                //     var target = event.target;
                //     if(!target.classList.contains('has-hint')) {
                //         this.show = false;
                //     }
                // }
            } else {
                this.show = true;
            }

        },
        methods: {
            showHint(){
                if(!this.isHover){
                    this.show = !this.show;
                }
            }
        }
    }
</script>

<style scoped>
    .hint-show-enter-active {
        display: flex;
        animation: hint-show 0.25s ease-out;
        animation-fill-mode: both
    }
    .hint-show-leave-active {
        display: none;
    }
    .has-hint--hover .hint{
        display: none;
    }
    .has-hint--hover:hover .hint{
        display: flex;
        animation: hint-show 0.25s ease-out;
        animation-fill-mode: both;
    }

    @keyframes hint-show{
        0%{
            opacity: 0;
            bottom: calc(100% + 6px)
        }
        80%{
            bottom: calc(100% + 12px)
        }
        100%{
            opacity: 1;
            bottom: calc(100% + 12px)
        }
    }

</style>